import { _decorator, Component, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Player')
export class Player extends Component {
  @property(Number) movementSpeed: number = 0.1;

  public movePlayer(offset: Vec3) {
    const newPosition = this.node.getPosition().add(offset.multiplyScalar(this.movementSpeed));
    this.node.setPosition(newPosition);
  }
}


